<template>
  <div>
    <div class="Input">
    <div style="margin:10px">
      <h2>Input your text to summarize</h2>
    </div>
    <div>
        <textarea placeholder="input" style="width:60%; border:solid 1px #d8d8d8; border-radius:20px; font-size:15px; padding:10px; background:transparent; color:white; outline:none;" rows = "10" @input="descInput" v-model="desc" />
        <span class="numberV">{{txtVal}}</span>
    </div>
        <span class="Min">
          <p style="font-size:18px">{{minlength}}</p>
          <input id = "minlength" type = "range" v-model="minlength" min="0" max="200" @input="MinStyle">
          <p style="font-size:18px">minlength</p>
        </span>
        <span class="Max">
          <p style="font-size:18px">{{maxlength}}</p>
          <input id = "maxlength" type = "range" v-model="maxlength" min="0" max="500" @input="MaxStyle">
          <p style="font-size:18px">maxlength</p>
        </span>
        <span class="Num_beams">
          <p style="font-size:18px">{{num_beams}}</p>
          <input id = "num_beams" type = "range" v-model="num_beams" min="0" max="10" @input="NumStyle">
          <p style="font-size:18px">num_beams</p>
        </span>
      </div>
    <div class="Button" style="position:relative;top:-140px">
      <button id = "generate" style = "cursor:pointer; position: relative;color: rgba(255, 255, 255, 1);;background-color: rgba(219, 87, 51, 1);;display: block;padding: 4px;border-radius: 8px;box-shadow: 0px 9px 0px rgba(219, 31, 5, 1), 0px 9px 25px rgba(0, 0, 0, .7);margin:auto;width: 120px;text-align: center;outline:none;border-color:rgba(219, 87, 51, 1);font-size:18px" v-on:click="generate()">generate</button>
    </div>
    <div class="Output" style="position:relative;top:-130px">
      <div style="margin:10px">
      <h2>Generated summarization:</h2>
    </div>
      <textarea style="width:60%; border:solid 1px #d8d8d8; border-radius:20px; font-size:15px; padding:10px; background:transparent; color:white;outline:none;" rows = "10" v-model="outputText" readonly />
      <span class="numberV">{{resultVal}}</span>
    </div>
  </div>
</template>
<style>
p{
  margin: 5px;
  font:sans-serif
}
.Min{
  position: relative;
  right:300px;
}
.Max{
  position: relative;
  top:-75px
}
.Num_beams{
  position:relative;
  left:300px;
  top:-150px;
}

input[type=range]{ 
     margin-top: 5px;
      outline: none; 
     -webkit-appearance: none;/*清除系统默认样式*/  
     width:10% !important;  
     background: -webkit-linear-gradient(#61bd12, #61bd12) no-repeat, #ddd;  
     background-size: 30% 100%;
    height: 3px;/*横条的高度*/  
} 
 /*拖动块的样式*/  
   input[type=range]::-webkit-slider-thumb {  
   -webkit-appearance: none;/*清除系统默认样式*/  
  height:16px;/*拖动块高度*/  
  width: 16px;/*拖动块宽度*/  
  background: #fff;/*拖动块背景*/  
  border-radius: 50%; /*外观设置为圆形*/  
   border: solid 1px #ddd; /*设置边框*/  
} 
</style>
<script>
import axios from "axios"

  export default{
    name : "first",
      data () {
        return {
            txtVal: 0,
            resultVal: 0,
            desc:"",
            outputText:"",
            maxlength:100,
            minlength:60,
            gold_summary:"",
            num_beams:4,
        }
      },
      methods: {
        MinStyle(){
          var minstyle= document.getElementById("minlength");
          minstyle.style["background-size"] = this.minlength/2+"% 100%";
        },
        MaxStyle(){
          var maxstyle= document.getElementById("maxlength");
          maxstyle.style["background-size"] = this.maxlength/5+"% 100%";
        },
        NumStyle(){
          var numstyle= document.getElementById("num_beams");
          numstyle.style["background-size"] = this.num_beams*10+"% 100%";
        },
        descInput(){
            this.txtVal = this.desc.trim().split(/\s+/).length
          },
          generate(){
            console.log(this.outputText);
            var button = document.getElementById("generate");
            button.style["box-shadow"] = "0px 3px 0px rgba(219, 31, 5, 1), 0px 3px 6px rgba(0, 0, 0, .9)";
            let data = {
              "content":this.desc,
              "minlength":this.minlength,
              "maxlength":this.maxlength,
              "num_beams":this.num_beams
            };
            console.log(data);
            axios.post("/snippet/",data).then(res=>{
              console.log(res)
              this.outputText = res.data.abstract;
              this.resultVal = this.outputText.trim().split(/\s+/).length;
              button.style["box-shadow"] = "0px 9px 0px rgba(219, 31, 5, 1), 0px 9px 25px rgba(0, 0, 0, .7)";
            })
          },
          //restrict to number
      }
  }
</script>



